body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.site-list-box>div:first-child>*:not(.MuiIconButton-root) {
  display: none;
}
.site-list-box.batch-edit>div:first-child>*:not(.MuiIconButton-root) {
  display: inline-flex;
}
.site-list-box .site-icon>span {
  display: none;
  float: left;
  position: absolute;
  z-index: 1;
}
.site-list-box .site-icon>span>svg {
  background: white;
}
.jse-theme-dark>.jse-main div.jse-menu {
  background: black;
  border-bottom: 1px solid #747474;
}
.site-list-box.batch-edit .site-icon>span {
  display: inline-flex;
}
@media screen and (max-width: 600px) {
  #root>.MuiBox-root {
    margin-left: 0px;
  }
  #root>.MuiBox-root>.MuiList-root {
    height: 65px;
    z-index: 9;
    width: 100%;
    max-width: unset;
    overflow: hidden;
  }
  #root>.MuiBox-root>[role="tabpanel"] {
    margin-top: 88px;
  }
  #root>.MuiBox-root>.MuiList-root:hover {
    height: unset;
  }
  #root>.MuiBox-root>.MuiList-root:hover~div {
    margin-top: 295px;
  }
  #root>.MuiBox-root>.MuiList-root:hover a {
    pointer-events: none;
  }
}

